* {
  font-family: 'Didact Gothic', sans-serif;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 45%;
  margin: auto;
}

.NavBar {
  display: flex;
  justify-content: space-between;
}

.NavBar button {
  padding: 10px 14px;
  width: 200px;
  vertical-align: center;
  background-color: transparent;
  border: none;
  font-size: larger;
  color: navy;
}

.NavBar button.selected {
  border-bottom: 2px solid orange;
}

.NavBar h1 {
  color: navy;
}

.Home {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Home h1 {
  color: navy;
  font-size: 100px;
  text-align: center;
  margin-top: 25%;
}

.Home .inputs {
  display: flex;
  justify-content: center;
}

.Home .inputs input {
  width: 300px;
}

.AutoCompleter {
  display: flex;
  justify-content: center;
}

.AutoCompleter .autocompleter-box {
  outline: 1px solid black;
  width: 306px;
  border: 2px solid thin black;
}

.AutoCompleter .placeholder {
  width: 24px;
  height: 27.5px;
  padding: 1px 6px;
  border: 2px solid transparent;
}

.autocompleter-box span {
  visibility: hidden;
}

.autocomplete-row {
  padding-left: 4px;
  padding-top: 4px;
}

.orange {
  color: orange;
}

.AdminPanel {
  position: absolute;
  left: 2px;
  border: 1px solid black;
}

p {
  font-size: larger;
}

.course-link {
  display: block;
}

.JobPanel {
  vertical-align: center;
  box-shadow: 0px 0px 10px gray;
  padding: 8px;
  margin-bottom: 4px;
  border-radius: 5px;
}

.RecruiterJobPanel {
  vertical-align: center;
  box-shadow: 0px 0px 10px gray;
  padding: 8px;
  margin-bottom: 4px;
  border-radius: 5px;
}

.profile-img {
  border-radius: 50%;
  border: 1px solid black;
  width: 250px;
  height: 250px;
}

.RecruiterContent img {
  border-radius: 50%;
  border: 1px solid black;
  width: 250px;
}

.Candidatecard {
  box-shadow: 0px 0px 10px gray;
  padding: 8px;
  margin-bottom: 4px;
  border-radius: 5px;
}

.JobListItem,
.RecruiterJobListItem {
  box-shadow: 2px 4px 4px gray;
  border: 1px solid gray;
  border-radius: 5px;
  padding: 10px;
  margin: 4px;
}

.CertificateCard {
  display: flex;
  flex-direction: column;
  border: 1px solid gray;
  box-shadow: 2px 4px 4px gray;
  padding: 10px;
  width: 114px;
  font-size: medium;
  margin-right: 6px;
}

.CertificateCard img {
  width: 100px;
  height: 100px;
}

.certificate-carousel {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 24px;
}

.actual-car {
  display: flex;
  width: 710px;
}

.green {
  color: green;
}

.blue-button {
  padding: 10px 15px;
  color: white;
  background-color: blue;
  border-radius: 5px;
  border: none;
}

.applied {
  display: flex;
  align-items: center;
}

.white {
  color: white;
}

.gray {
  color: gray;

}

.blue {
  color: blue;

}
.applicant-container { 
  overflow:scroll;
}
.RecruiterApplicantRow {
  padding: 4px;
  display: flex;
  align-items: center;
  width: 300px;
  justify-content: space-between;
  border:1px solid gray;
  box-shadow: 2px 5px 5px gray;
  border-radius:5px;
  margin-bottom:4px;
}

.red {
  color: red;
}